import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Title } from '@angular/platform-browser';
import { MatDialog } from '@angular/material/dialog';
import { DetailService } from "../shared/detail.service";
import { DeleteConfirmationComponent } from "../../ui/modals/components/delete-confirmation.component";
import { TranslocoService } from '@ngneat/transloco';

@Component({
    templateUrl: 'base-detail.component.html'
})

export class BaseDetailComponent implements OnInit {

    public data: object;
    public submited: boolean = false;
    public deleted: boolean = false;

    constructor(public detailService: DetailService, protected route: ActivatedRoute, protected translocoService: TranslocoService, public dialog: MatDialog, public titleService: Title) {}

    public ngOnInit(): void {
        this.initializeForm(this.route.snapshot.data['form']);
    }

    public submit(form): void
    {
        if(!form.invalid) {
            this.data['_token'] = this.detailService.getCsrf();
            this.detailService.sendForm(this.data).subscribe(result => {
                if(!!result['csrf'] && !!result['type'] && !!result['properties']) {
                    this.initializeForm(result)
                }
                this.submited = false
            })
        }
    }

    delete(): void
    {
        this.deleted = true;

        const dialogRef = this.dialog.open(DeleteConfirmationComponent, {
            width: '400px',
            data: {text: this.detailService.getDeleteConfirmationText(), confirm: true}
        });

        dialogRef.afterClosed().subscribe(confirm => {
            if(confirm) {
                this.detailService.delete().subscribe(result => this.deleted = false);
            } else {
                this.deleted = false
            }
        });
    }

    public change(field): void
    {
        if(field.hasOwnProperty('update') && field['update']) {
            this.detailService.sendForm(this.data).subscribe(result => {
                if(!!result['csrf'] && !!result['type'] && !!result['properties']) {
                    this.initializeForm(result);
                }
            });
        }
    }

    private initializeForm(form): void
    {
      this.detailService.setForm(form);
      this.detailService.setOperation(this.route.snapshot.data['operation']);

      this.translocoService.selectTranslate(this.detailService.getTitle()).subscribe(message => {
        this.route.snapshot.data['title'] = message;
        this.titleService.setTitle(`${message} - Zentlix`);
      });

      this.data = this.detailService.initializeDataObj();
    }
}
