import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { map } from 'rxjs/operators';
import { RouteLoaderService } from '../shared/routes.service';

@Injectable({
  providedIn: 'root'
})
export class IsRouteCheckedGuard implements CanActivate {
  constructor(private router:Router, private routeLoaderService:RouteLoaderService){}
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    return this.routeLoaderService.loadLazyResourceModule()
      .pipe(
        map(data => {
          const config = this.router.config;

          this.router.config[2]['children'].push(...data);
          this.router.resetConfig(config);

          for (let route of data) {
            if(route.path.includes(':')) {
              let pathParamIdx = route.path.split('/').findIndex((segment) => segment.includes(':'));
              if(!!next.url[pathParamIdx]) {
                route.path = route.path.replace(route.path.split('/')[pathParamIdx], next.url[pathParamIdx].path);
              }
            }

            let path = next.url.map((segment) => segment.path).join('/');

            if(route.path === path) {
              this.router.navigateByUrl(state.url);
              return false;
            }
          }

          return true;
        })
      );
  }
}
