import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Title } from '@angular/platform-browser';
import { TranslocoService } from '@ngneat/transloco';
import { HtmlResolver } from '../shared/html.resolver';

@Component({
    templateUrl: 'base-html.component.html',
    providers: [HtmlResolver]
})
export class BaseHtmlComponent implements OnInit {

  public data: string;

  constructor(protected route: ActivatedRoute, protected translocoService: TranslocoService, private titleService: Title) {}

  public ngOnInit(): void {
    this.data = this.route.snapshot.data['html']['html'];

    this.translocoService.selectTranslate(this.route.snapshot.data['html']['title']).subscribe(message => {
      this.route.snapshot.data['html']['title'] = message;
      this.titleService.setTitle(`${message} - Zentlix`);
      this.route.snapshot.data['title'] = message;
    });
  }
}
