import { Injectable } from '@angular/core';
import { Router, Route } from '@angular/router';
import { HttpClient } from "@angular/common/http";
import { Observable } from 'rxjs';
import { resolvers } from '../../../environments/resolvers';
import { components } from '../../../environments/components';
import { environment } from '../../../environments/environment';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class RouteLoaderService {

  constructor(private router: Router, private http: HttpClient) {}

  public loadLazyResourceModule(url = null): Observable<any> {
    return this.http.get<Route[]>(`/${environment.baseUrl}/routes/all`)
      .pipe(
        map((routes: Route[], idx) => {
          for (let index in routes) {
              if (routes[index].hasOwnProperty('component')) {
                  routes[index].component = components[routes[index]['component'].toString()];
              }
              if (routes[index].hasOwnProperty('resolve')) {
                  if (routes[index].resolve.hasOwnProperty('config')) {
                      routes[index].resolve.config = resolvers.ListResolver;
                  }
                  if (routes[index].resolve.hasOwnProperty('form')) {
                      routes[index].resolve.form = resolvers.DetailResolver;
                  }
                  if (routes[index].resolve.hasOwnProperty('state')) {
                      routes[index].resolve.state = resolvers.StateResolver;
                  }
                  if (routes[index].resolve.hasOwnProperty('html')) {
                      routes[index].resolve.html = resolvers.HtmlResolver;
                  }
              }
          }

          return routes;
        })
      )
  }

  public isRoutesLoaded(): boolean {
      return this.router.config[2]['children'].length > 1
  }
}
