import { Component, OnInit, OnDestroy, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, Params } from "@angular/router";
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { distinctUntilChanged, tap, takeUntil } from 'rxjs/operators';
import { merge, fromEvent, Subject } from "rxjs";
import { ListDataSource } from "../shared/list.datasource";
import { ListService } from "../shared/list.service";
import { ListResolver } from "../shared/list.resolver";
import { TranslocoService } from '@ngneat/transloco';

@Component({
    templateUrl: 'base-list.component.html',
    providers: [ListDataSource, ListService, ListResolver]
})
export class BaseListComponent implements OnInit, OnDestroy, AfterViewInit {

    public dataSource: ListDataSource;
    private destroy$ = new Subject<undefined>();
    public token: any;

    constructor(protected route: ActivatedRoute, protected translocoService: TranslocoService, public listService: ListService, public titleService: Title) {}

    @ViewChild(MatSort, {static: true}) sort: MatSort;
    @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
    @ViewChild('input', { static: true }) input: ElementRef;

    public ngOnInit() {
        this.route.params.pipe(
            takeUntil(this.destroy$)
        ).subscribe((params: Params) => {
            this.dataSource = new ListDataSource(this.listService);
            this.listService.setList(this.route.snapshot.data['config']);
            this.translocoService.selectTranslate(this.listService.list.title).subscribe(message => {
              this.route.snapshot.data['title'] = message;
              this.titleService.setTitle(`${message} - Zentlix`);
            });

            this.token = this.listService.list.token;
            this.loadPage();
        });
    }

    public ngOnDestroy(): void {
        this.destroy$.next();
        this.destroy$.complete();
    }

    public ngAfterViewInit() {

        this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);

        fromEvent(this.input.nativeElement,'keyup')
            .pipe(
                distinctUntilChanged(),
                tap(() => {
                    this.paginator.pageIndex = 0;

                    this.loadPage();
                })
            )
            .subscribe();

        merge(this.sort.sortChange, this.paginator.page)
            .pipe(
                tap(() => this.loadPage())
            )
            .subscribe();

    }

    loadPage() {
        let pageSize = this.paginator.pageSize ? this.paginator.pageSize : 20;
        this.dataSource.loadList(
            this.input.nativeElement.value,
            this.sort.active,
            this.sort.direction,
            this.paginator.pageIndex * pageSize,
            pageSize);
    }
}
