import { Component, Input, Output, EventEmitter } from '@angular/core';
import * as ClassicEditor from '../shared/editor/ckeditor';
import '../shared/editor/ru';
import { ControlContainer, NgForm } from '@angular/forms';
import { FieldService } from '../shared/field.service';
import { Form } from '../../../base/shared/form';
import { TranslocoService } from '@ngneat/transloco';
import { Select2OptionData } from 'ng-select2';

@Component({
    selector: 'field-component',
    templateUrl: 'field.component.html',
    providers: [FieldService],
    viewProviders: [{provide: ControlContainer, useExisting: NgForm}]
})
export class FieldComponent {

    @Input() model: object = {};
    @Input() field: Form;
    @Input() required: string[] = [];

    @Output() modelChange: EventEmitter<any> = new EventEmitter<any>();

    public Editor = ClassicEditor;
    constructor(public fieldService: FieldService, private translocoService: TranslocoService) {}

    public getOptions(): Select2OptionData[]
    {
      let options = [] as Select2OptionData[];
      for (let index in this.field['enum']) {
        options.push({
          id: this.field['enum'][index],
          text: this.translocoService.translate(this.field['enum_titles'][index]),
          disabled: this.fieldService.isDisabledSelectOption(this.field['enum'][index], this.field)
        })
      }

      return options;
    }

    public emitInputValue()
    {
      if(this.fieldService.getInputType(this.field) === 'number') {
        this.model[this.field['name']] = parseFloat(this.model[this.field['name']]);
      }
      this.modelChange.emit(this.model)
    }
}
