import { Injectable } from '@angular/core';
import { MatPaginatorIntl } from '@angular/material/paginator';
import { TranslocoService } from '@ngneat/transloco';

@Injectable()
export class MatPaginationIntlService extends MatPaginatorIntl {
    translate: TranslocoService;
    firstPageLabel = 'First page';
    itemsPerPageLabel = 'On page:';
    lastPageLabel = 'Last page';
    nextPageLabel = 'Next page';
    previousPageLabel = 'Previous page';
    of = 'of';

    getRangeLabel = (page: number, pageSize: number, length: number): string => {
        this.translate.selectTranslateObject('zentlix_main.datatable').subscribe(messages => {
          this.of = messages['of'];
          this.changes.next();
        });

        if (length === 0 || pageSize === 0) {
            return '0 ' + this.of + ' ' + length;
        }
        length = Math.max(length, 0);
        const startIndex = ((page * pageSize) > length) ?
            (Math.ceil(length / pageSize) - 1) * pageSize:
            page * pageSize;

        const endIndex = Math.min(startIndex + pageSize, length);

        return startIndex + 1 + ' - ' + endIndex + ' ' + this.of + ' ' + length;
    };

    injectTranslateService(translate: TranslocoService) {
        this.translate = translate;

        this.translate.langChanges$.subscribe(() => {
          this.translateLabels();
        });

        this.translateLabels();
    }

    public translateLabels()
    {
      this.translate.selectTranslateObject('zentlix_main.datatable').subscribe(messages => {
        this.firstPageLabel = messages['first_page'];
        this.itemsPerPageLabel = messages['items_per_page'];
        this.lastPageLabel = messages['last_page'];
        this.nextPageLabel = messages['next_page'];
        this.previousPageLabel = messages['previous_page'];

        this.changes.next();
      });
    }
}
