import { Injectable } from '@angular/core';
import { Form } from '../../../base/shared/form';
import { environment } from 'environments/environment';
import { TranslocoService } from '@ngneat/transloco';

export interface IEditorConfig {
    language: string,
    simpleUpload: ISimpleUploaderConfig
}

export interface ISimpleUploaderConfig {
    uploadUrl: string,
    headers: object
}

@Injectable()
export class FieldService {

    constructor(private translocoService: TranslocoService) {}

    public defaultEditorConfig: IEditorConfig = {
        language: this.translocoService.getActiveLang(),
        simpleUpload: {
            uploadUrl: environment.routes.fileUpload,
            headers: {
                'X-Requested-With': 'XMLHttpRequest'
            }
        },
    };

    public isDisabled = (field: Form): boolean => !!field.disabled && field.disabled;
    public isInput = (type: string): boolean => type === 'string' || type === 'integer' || type === 'number';
    public isTextarea = (type: string): boolean => type === 'textarea';
    public isDataTable = (type: string): boolean => type === 'datatable';
    public isSelect = (type: string): boolean => type === 'array';
    public isCheckbox = (type: string): boolean => type === 'boolean';
    public isEditor = (type: string): boolean => type === 'editor';
    public isFileUploader = (type: string): boolean => type === 'file';

    public isNotice = (type: string): boolean => type === 'notice';
    public isInfoNotice = (type): boolean => type === 'info';
    public isSuccessNotice = (type): boolean => type === 'success';
    public isWarningNotice = (type): boolean => type === 'warning';
    public isDangerNotice = (type): boolean => type === 'danger';

    public isRequired(required: string[], field: string): boolean {
        if (!required) {
            return false;
        }

        return required.includes(field);
    }

    public getInputType(field: Form): string {
      if (field.type === 'number' || field.type === 'integer') {
        return 'number';
      }

      if (field.hasOwnProperty('widget')) {
        return field.widget;
      }

      return 'text';
    }

    public getPattern(type: string): string {
        if (type === 'number') {
            return '^[0-9]*[.,]?[0-9]+$';
        }

        if (type === 'integer') {
            return '^[0-9]*$';
        }

        return '';
    }

    public isDisabledSelectOption(value, field): boolean {
        if (field.hasOwnProperty('disabled_values')) {
            return field.disabled_values.includes(value);
        }

        return false;
    }

    public getEditorConfig(field: Form): IEditorConfig {
        if (field.hasOwnProperty('config')) {
            return field.config as IEditorConfig;
        }

        return this.defaultEditorConfig;
    }
}
