import {Injectable} from '@angular/core';
import {Observable} from "rxjs/internal/Observable";
import {HttpClient, HttpParams} from "@angular/common/http";

interface Column {
  data: string,
  orderable: boolean,
  searchable: boolean,
  visible: boolean,
  className: string,
  title: string,
  translate: boolean
}

interface List {
    options: ListOptions,
    resource: string,
    title: string,
    recordsTotal: number,
    createBtnLabel?: string,
    token?: string[],
    createUrl?: string
}

interface ListOptions{
    pageLength: number,
    lengthMenu: number[],
    columns: Column[],
}

@Injectable()
export class ListService {

    private resource: string;
    public list: List;
    public columnLabels = {};
    public displayedColumns: string[];

    constructor(private http: HttpClient) {}

    public loadPage(filter: string = '', sort: string = '', sortDirection: string = 'desc', start:number = 0, length: number = 20): Observable<Object>
    {
        return this.http.get(this.resource, {
            params: new HttpParams()
                .set('_init', '0')
                .set('_dt', this.resource)
                .set('filter', filter)
                .set('sort', sort)
                .set('sortDirection', sortDirection)
                .set('start', start.toString())
                .set('length', length.toString())
        })
    }

    public loadResource(url: string): Observable<Object>
    {
        this.resource = url;

        return this.http.get(url, {
            params: new HttpParams()
                .set('_init', '1')
                .set('_dt', url)
        });
    }

    public setColumns(): void
    {
        this.columnLabels = [];
        this.displayedColumns = [];
        for(let column of this.list.options.columns) {
            if(column.visible) {
                this.columnLabels[column.data] = column.title;
                this.displayedColumns.push(column.data)
            }
        }
    }

    public setList(list: List): void
    {
        this.list = list;

        this.setColumns()
    }

    public isSortable(currentColumn): boolean
    {
        for(let column of this.list.options.columns) {
            if(column.data === currentColumn) {
                return column.orderable == true;
            }
        }
    }

    public isNeedTranslate(currentColumn): boolean
    {
      for(let column of this.list.options.columns) {
        if(column.data === currentColumn) {
          return column.translate === true;
        }
      }
    }
}
