import {Injectable} from "@angular/core";
import {CollectionViewer, DataSource} from "@angular/cdk/collections";
import {Observable, BehaviorSubject, of} from "rxjs";
import {ListService} from "./list.service";
import {catchError, finalize, tap} from "rxjs/operators";

@Injectable()
export class ListDataSource implements DataSource<any> {

    private loadingSubject = new BehaviorSubject<boolean>(false);
    private emptySubject = new BehaviorSubject<boolean>(false);
    public listSubject = new BehaviorSubject<any>([]);
    public loading$ = this.loadingSubject.asObservable();
    public empty$ = this.emptySubject.asObservable();

    constructor(private listService: ListService) {}

    loadList(filter:string, sort: string, sortDirection:string, start:number, length:number) {

        this.loadingSubject.next(true);

        this.listService.loadPage(filter, sort, sortDirection, start, length)
            .pipe(
                tap(val => {
                    if(Array.isArray(val)) {
                        this.emptySubject.next(val.length === 0);
                    }
                }),
                catchError(() => of([])),
                finalize(() => {
                    this.loadingSubject.next(false);
                })
            )
            .subscribe(resource => this.listSubject.next(resource));
    }

    connect(collectionViewer: CollectionViewer): Observable<any[]> {
        return this.listSubject.asObservable();
    }

    disconnect(collectionViewer: CollectionViewer): void {
        this.listSubject.complete();
        this.loadingSubject.complete();
        this.emptySubject.complete();
    }
}

