import {Injectable} from '@angular/core';
import {FlatTreeControl} from '@angular/cdk/tree';
import {MatTreeFlatDataSource, MatTreeFlattener} from '@angular/material/tree';
import {Form} from "../../../base/shared/form";

interface Node {
    name: string;
    children?: Node[];
    content?: object
}

interface FlatNode {
    expandable: boolean;
    name: string;
    level: number;
    content?: object
}

@Injectable()
export class TreeService {

    public dataSource;
    private tree;

    private _transformer = (node: Node, level: number): FlatNode  => {
        return {
            expandable: !!node.children && node.children.length > 0,
            name: node.name,
            level: level,
            content: node.content
        };
    };

    hasChild = (_: number, _nodeData: FlatNode): boolean => _nodeData.expandable;

    public set(tree: Form): void
    {
        this.tree = tree;
        this.init();
    }

    private init(): void
    {
        this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
        let data: Node[] = [];
        for (let element in this.tree.tree_group) {
            let children: Node[] = [];
            for (let childrenKey of this.tree.tree_group[element]) {
                for (let property of this.tree.properties) {
                    if (property['name'] === childrenKey) {
                        children.push({name: property['title'], content: property});
                    }
                }
            }
            data.push(
                {name: element, children: children}
            );
        }

        this.dataSource.data = data;
    }
    treeControl = new FlatTreeControl<FlatNode>(
        node => node.level, node => node.expandable);

    treeFlattener = new MatTreeFlattener(
        this._transformer, node => node.level, node => node.expandable, node => node.children);
}