import { Component } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { NavData } from '../shared/_nav';
import { Locale } from '../shared/_locale';
import { User } from '../shared/_user';
import { Notification } from '../shared/_notification';
import { TranslocoService } from '@ngneat/transloco';
import { HttpClient } from "@angular/common/http";
import { environment } from 'environments/environment';

@Component({
  selector: 'app-dashboard',
  templateUrl: './default-layout.component.html'
})
export class DefaultLayoutComponent {
    public sidebarMinimized = false;
    public navItems: NavData[] = [] ;
    public locales: Locale[] = [];
    public notifications: Notification[] = [];
    public currentLocale: Locale;
    public user: User;
    public logoutUrl: string;
    public routes: object;

    constructor(private route: ActivatedRoute, private http: HttpClient, public translocoService: TranslocoService) {
      this.navItems = this.route.snapshot.data['state']['sidebar'];
      this.locales = this.route.snapshot.data['state']['locales'];
      this.user = this.route.snapshot.data['state']['user'];
      this.logoutUrl = this.route.snapshot.data['state']['logoutUrl'];
      this.notifications = this.route.snapshot.data['state']['notifications'];
      this.currentLocale = this.locales.find(x => x.id === this.route.snapshot.data['state']['currentLocale']);
      this.translocoService.setActiveLang(this.currentLocale.code);

      this.translocoService.selectTranslateObject('zentlix_main').subscribe(messages => {
        this.route.snapshot.data['title'] = messages['control_panel']
      });

      this.routes = environment.routes;
    }

  toggleMinimize(e) {
    this.sidebarMinimized = e;
  }

  public changeLanguage(lang) {
      this.translocoService.setActiveLang(lang);
      this.currentLocale = this.locales.find(x => x.code === lang);

      this.http.post(environment.routes.changeLocale, {locale_id: this.currentLocale.id}).subscribe();
  }
}
