import { Component, Input, Output, ViewChild, EventEmitter, AfterViewInit } from '@angular/core';
import { DropzoneComponent,  DropzoneConfigInterface } from 'ngx-dropzone-wrapper';
import { File } from '../shared/_file';

@Component({
    selector: 'file-component',
    templateUrl: 'file.component.html'
})
export class FileComponent implements AfterViewInit {

  @Input() savePath: string;
  @Input() config: DropzoneConfigInterface;
  @Input() model: File[] | null;
  @Output() modelChange: EventEmitter<string> = new EventEmitter<string>();
  @ViewChild(DropzoneComponent, { static: false }) componentRef?: DropzoneComponent;

  ngAfterViewInit() {
    if(!!this.model) {
      this.model.forEach(image => {
        this.setImage(image)
      });
    }
  }

  private setImage(image: File): void {

    const mockFile = {
      id: image.id,
      name: image.name,
      size: image.size,
      type: image.mime,
      accepted: true,
      dataURL: image.path
    };

    const dropzoneInstance = this.componentRef.directiveRef.dropzone();

    dropzoneInstance.emit('addedfile', mockFile);
    dropzoneInstance.files.push(mockFile);
    dropzoneInstance.createThumbnailFromUrl(mockFile, this.config.thumbnailWidth, this.config.thumbnailHeight, this.config.thumbnailMethod, true, thumbnail => {
      dropzoneInstance.emit('thumbnail', mockFile, thumbnail);
      },
      'Anonymous'
    );

    dropzoneInstance.emit('complete', mockFile);

    this.emitValue()
  }

  public onUploadSuccess(event): void {

    const dropzoneInstance = this.componentRef.directiveRef.dropzone();

    let index = dropzoneInstance.files.findIndex((file) => file.dataURL === event[0]['dataURL']);
    dropzoneInstance.files[index]['dataURL'] = event[1]['path'];
    dropzoneInstance.files[index]['id'] = event[1]['id'];

    this.emitValue()
  }

  public onSending(event): void {
    if(!!this.savePath) {
      event[2].append('savePath', this.savePath)
    }
  }

  public onRemovedFile(file): void {
    const dropzoneInstance = this.componentRef.directiveRef.dropzone();

    dropzoneInstance.files.filter(item => item.id !== file.id);

    this.emitValue()
  }

  public onUploadError(event): void {
    console.error(event)
  }

  private emitValue(): void {
    const dropzoneInstance = this.componentRef.directiveRef.dropzone();

    if(this.config.uploadMultiple) {
      this.modelChange.emit(dropzoneInstance.files.map(element => element.id))
    } else {
      console.log(dropzoneInstance.files.map(element => element.id)[0]);
      this.modelChange.emit(dropzoneInstance.files.map(element => element.id)[0])
    }
  }
}
