import { Component, OnInit } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { Title } from '@angular/platform-browser';
import { TranslocoService } from '@ngneat/transloco';
import { ActivatedRoute } from '@angular/router';
import { environment } from 'environments/environment';
import { CardWidget } from '../shared/widgets/_card';
import { TableWidget } from '../shared/widgets/_table';
import { WidgetsService } from '../shared/widgets.service';

@Component({
  templateUrl: 'dashboard.component.html',
  providers: [WidgetsService]
})
export class DashboardComponent implements OnInit {

  public widgets: CardWidget[] | TableWidget[] = [];

  public constructor(private translocoService: TranslocoService,
                     private http: HttpClient,
                     private titleService: Title,
                     protected route: ActivatedRoute,
                     public widgetsService: WidgetsService) {}

  ngOnInit(): void {

    this.translocoService.selectTranslateObject('zentlix_main').subscribe(messages => {
      this.route.snapshot.data['title'] = messages['dashboard'];
      this.titleService.setTitle(`${messages['control_panel']} - Zentlix`);
    });

    this.loadWidgets()
  }

  private loadWidgets(): void
  {
    this.http.post(`/${environment.baseUrl}/dashboard/widgets`, {}).subscribe(result => {this.widgets = result['widgets']});
  }
}
