import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DefaultLayoutComponent } from './containers';
import { P404Component } from "./error/components/404.component";
import { StateResolver } from "./containers/shared/state.resolver";
import { IsRouteCheckedGuard } from './core/guards/route_checked.guard';

export const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full',
  },
  {
    path: '404',
    component: P404Component,
    data: {
      title: 'Page 404'
    }
  },
  {
    path: '',
    component: DefaultLayoutComponent,
    resolve: {
      state: StateResolver
    },
    children: [
      {
        path: 'dashboard',
        loadChildren: () => import('./dashboard/dashboard.module').then(m => m.DashboardModule)
      }
    ]
  },
  { path: '**',
    component: P404Component,
    canActivate: [IsRouteCheckedGuard]
  }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes,  {useHash: true}) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
