import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { HttpEvent, HttpInterceptor, HttpHandler, HttpRequest, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { Router } from "@angular/router";
import { tap, catchError, finalize } from "rxjs/operators";
import { of } from "rxjs/internal/observable/of";
import { FlashMessagesService } from 'angular2-flash-messages';
import { environment } from 'environments/environment';
import { LoaderService } from '../../ui/loader/shared/loader.service';
import { TranslocoService } from '@ngneat/transloco';

@Injectable()
export class ResponseInterceptor implements HttpInterceptor {
    constructor(private flashService: FlashMessagesService, private router: Router, private translocoService: TranslocoService, public loaderService: LoaderService) {}

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

        return next.handle(req).pipe(
            tap(evt => {
              if (evt instanceof HttpResponse) {
                if(!!evt.body['message']) {
                  this.flashService.show(
                    evt.body['message'], {
                      cssClass: evt.body['success'] ? 'alert-success' : 'alert-danger',
                      timeout: 6000,
                      showCloseBtn: true
                    });
                }

                if(!!evt.body['redirect']) {
                  this.router.navigate([evt.body['redirect'].replace(`/${environment.baseUrl}`, '')]);
                }
              }
            }),
            finalize(() => this.loaderService.hide()),
            catchError((err: any) => {
                if(err instanceof HttpErrorResponse) {
                  try {
                    if(err.url.includes('/login')) {
                      window.location.href = '/backend/login';
                    }
                    if(err.status === 404) {
                      this.router.navigateByUrl('/404/');
                    }
                    this.flashService.show(!!err.error.message ? this.translocoService.translate(err.error.message) : this.translocoService.translate('zentlix_main.system_error'), {
                      cssClass: 'alert-danger', timeout: 6000, showCloseBtn: true
                    });
                  } catch (e) {
                    this.flashService.show(this.translocoService.translate('zentlix_main.system_error'), {cssClass: 'alert-danger', timeout: 6000, showCloseBtn: true});
                  }
                }
                return of(err);
            })
        );
    }
}
