import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { LocationStrategy, HashLocationStrategy } from '@angular/common';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { TranslocoRootModule } from './core/modules/transloco-root.module';

import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { PERFECT_SCROLLBAR_CONFIG } from 'ngx-perfect-scrollbar';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true
};

import {
  AppAsideModule,
  AppBreadcrumbModule,
  AppHeaderModule,
  AppFooterModule,
} from '@coreui/angular';

// Import 3rd party components
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { TabsModule } from 'ngx-bootstrap/tabs';
import { ChartsModule } from 'ng2-charts';
import { FlashMessagesModule, FlashMessagesService } from 'angular2-flash-messages';

// Import Zentlix modules
import { LoaderModule } from "./ui/loader/loader.module";
import { ErrorModule } from "./error/error.module";
import { ResourceModule } from './resource/resource.module';
import { AppSidebarModule } from './lib/sidebar';

import { HeadersInterceptor } from "./core/interceptors/headers.interceptor";
import { ResponseInterceptor } from "./core/interceptors/response.interceptor";
import { AppComponent } from './app.component';
import { DefaultLayoutComponent } from './containers';
import { AppRoutingModule } from './app.routing';
import { StateResolver } from "./containers/shared/state.resolver";
import { ListComponent } from './resource/components/list.component';
import { DetailComponent } from './resource/components/detail.component';
import { HtmlComponent } from './resource/components/html.component';

@NgModule({
  imports: [
      BrowserModule,
      BrowserAnimationsModule,
      HttpClientModule,
      AppRoutingModule,
      AppAsideModule,
      AppBreadcrumbModule.forRoot(),
      AppFooterModule,
      AppHeaderModule,
      AppSidebarModule,
      PerfectScrollbarModule,
      BsDropdownModule.forRoot(),
      TabsModule.forRoot(),
      ChartsModule,
      FlashMessagesModule,
      LoaderModule,
      ErrorModule,
      TranslocoRootModule,
      ResourceModule
  ],
  declarations: [
    AppComponent,
    DefaultLayoutComponent
  ],
  providers: [
      {
          provide: LocationStrategy,
          useClass: HashLocationStrategy
      },
      {
          provide: HTTP_INTERCEPTORS,
          useClass: HeadersInterceptor,
          multi: true,
      },
      {
          provide: HTTP_INTERCEPTORS,
          useClass: ResponseInterceptor,
          multi: true,
      },
      StateResolver,
      FlashMessagesService
  ],
  bootstrap: [ AppComponent ],
  entryComponents: [ListComponent, DetailComponent, HtmlComponent]
})
export class AppModule { }
