import { Injectable } from '@angular/core';
import { Router } from "@angular/router";
import { Observable } from "rxjs/internal/Observable";
import { HttpClient } from "@angular/common/http";
import { Location } from '@angular/common';
import { environment } from 'environments/environment';
import { Form } from "./form";

@Injectable()
export class DetailService {

    private simple_form;
    private flex_2_form;
    private form = {} as Form;
    private operation: string = 'create';
    private formModel: object = {};
    public data: object = {};

    constructor(private http: HttpClient, private router: Router, private location: Location) {}

    public getRootRequired = (): string[] => this.form.required;

    public getTitle = (): string => this.form.title;

    public getCsrf = (): string => this.form.csrf;

    public goBack = (): void => this.location.back();

    public getFormProperties = (): Form[] => this.form.properties;

    public isEdit = (): boolean => this.operation === 'edit';

    public allowDelete = (): boolean => this.operation === 'edit' && this.form.disable_delete !== true;

    public isObj = (type: string): boolean =>  type === 'object';

    public isSingleTabForm = (): boolean => this.form.form === this.flex_2_form || this.form.form === this.simple_form;

    public isTree = (form: Form): boolean => !!form.form && form.form === 'tree';

    public loadForm(url: string): Observable<Object>
    {
        return this.http.get(url)
    }

    public sendForm(data: object)
    {
        return this.http.post(`/${environment.baseUrl}/${this.router.url}`, data)
    }

    public delete()
    {
        return this.http.post(`/${environment.baseUrl}${this.router.url.replace('edit', '')}delete`, {});
    }

    public setForm(form)
    {
        this.setDefaults();

        this.form = form;
    }

    public getDeleteConfirmationText(): string
    {
        if(!!this.form.deleteConfirm) {
            return this.form.deleteConfirm;
        }

        return 'zentlix_main.delete_element_confirm';
    }

    public getDeleteBtnLabel(): string
    {
        if(!!this.form.deleteBtnLabel) {
            return this.form.deleteBtnLabel;
        }

        return 'zentlix_main.delete_element';
    }

    public setOperation(operation: string)
    {
        this.operation = operation;
    }

    public getSubmitBtnLabel(): string
    {
        if(this.operation === 'create') {
            return 'zentlix_main.create';
        }

        return 'zentlix_main.save';
    }

    public getSingleFormClass(): string
    {
        if(this.form.form === this.flex_2_form) {
            return 'col-sm-6';
        }

        return 'col-sm-12';
    }

    public initializeDataObj()
    {
        Object.keys(this.form.properties).forEach(key => {
            this.formModel = this.setNestedForm(this.form.properties[key], this.formModel)
        });

        return this.formModel;
    }

    private setNestedForm(form: Form, model: object): object
    {
        if(form.hasOwnProperty('properties')) {
            if(form.hasOwnProperty('name')) {
                if(!model.hasOwnProperty(form.name)) {
                    model[form.name] = {};
                }
                Object.keys(form.properties).forEach(key => {
                    model[form.name] = this.setNestedForm(form.properties[key], model[form.name]);
                });
            }
        } else {
            if(!!form.name && form.type !== 'notice') {
                if(form.type === 'data') {
                    this.data[form.name] = form.value;
                } else {
                    model[form.name] = form?.value;
                }
            }
        }

        return model
    }

    private setDefaults()
    {
        this.simple_form = 'simple_form';
        this.flex_2_form = 'flex_2_form';
        this.form = {} as Form;
        this.formModel = {};
    }
}
