import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { TranslocoService } from '@ngneat/transloco';

@Component({
  templateUrl: '404.component.html'
})
export class P404Component implements OnInit {
  constructor(private titleService: Title, private translocoService: TranslocoService) {}

  public ngOnInit() {
    this.translocoService.selectTranslate('zentlix_main.page_not_found').subscribe(message => {
      this.titleService.setTitle(`${message} - Zentlix`);
    });
  }
}
