import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { BehaviorSubject } from "rxjs/index";
import { MatSort } from "@angular/material/sort";
import { MatPaginator } from "@angular/material/paginator";
import { environment } from 'environments/environment';
import { DataTable } from "../shared/datatable";

@Component({
    selector: 'datatable-component',
    templateUrl: 'datatable.component.html'
})
export class DatatableComponent implements OnInit {
    public baseUrl = environment.baseUrl;
    public dataSource: MatTableDataSource<any>;
    private emptySubject = new BehaviorSubject<boolean>(false);
    public empty$ = this.emptySubject.asObservable();

    @Input() data: DataTable;
    @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
    @ViewChild(MatSort, {static: true}) sort: MatSort;

    ngOnInit(): void
    {
        this.dataSource = new MatTableDataSource(this.data.value);
        this.emptySubject.next(this.data.value.length === 0);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
    }

    applyFilter(filterValue: string): void
    {
        this.dataSource.filter = filterValue.trim().toLowerCase();

        if (this.dataSource.paginator) {
            this.dataSource.paginator.firstPage();
        }
    }
}
