import {NgModule} from '@angular/core';
import {CommonModule} from "@angular/common";
import {FormsModule, ReactiveFormsModule} from '@angular/forms';

import {DirectivesModule} from "../core/directives/directives.module";
import {DeleteConfirmationModule} from "../ui/modals/delete-confirmation.module";
import {MaterialModule} from "../ui/material/material.module";
import {BaseListComponent} from "./components/base-list.component";
import {ListService} from "./shared/list.service";
import {ListDataSource} from "./shared/list.datasource";
import {ListResolver} from "./shared/list.resolver";

@NgModule({
    imports: [
        CommonModule,
        FormsModule,
        ReactiveFormsModule,
        MaterialModule,
        DirectivesModule,
        DeleteConfirmationModule,
    ],
    exports: [MaterialModule, DirectivesModule, DeleteConfirmationModule, BaseListComponent],
    declarations: [BaseListComponent],
    providers: [ListResolver, ListService, ListDataSource]
})

export class ListModule {}