import {NgModule} from '@angular/core';
import {CommonModule} from "@angular/common";
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {DirectivesModule} from "../core/directives/directives.module";
import {DeleteConfirmationModule} from "../ui/modals/delete-confirmation.module";
import {MaterialModule} from "../ui/material/material.module";
import {BaseDetailComponent} from "./components/base-detail.component";
import {DetailResolver} from "./shared/detail.resolver";
import {DetailService} from "./shared/detail.service";
import {FormModule} from "../ui/form/form.module";

@NgModule({
    imports: [
        CommonModule,
        FormsModule,
        ReactiveFormsModule,
        MaterialModule,
        DirectivesModule,
        DeleteConfirmationModule,
        FormModule
    ],
    exports: [FormModule, MaterialModule, DirectivesModule, DeleteConfirmationModule, BaseDetailComponent],
    declarations: [BaseDetailComponent],
    providers: [DetailResolver, DetailService]
})

export class DetailModule { }