import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd } from '@angular/router';
import { RouteLoaderService } from './core/shared/routes.service';

@Component({
  selector: 'body',
  template: '<router-outlet></router-outlet>'
})
export class AppComponent implements OnInit {
  constructor(private routeLoaderService: RouteLoaderService, private router: Router) { }

  ngOnInit() {
    this.router.events.subscribe((evt) => {
      if (evt instanceof NavigationStart) {
        if (!this.routeLoaderService.isRoutesLoaded()) {
          this.loadRoutes();
        }
      }

      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      window.scrollTo(0, 0);
    });
  }

  private loadRoutes(): void {
    this.routeLoaderService.loadLazyResourceModule().subscribe(data => {

      const config = this.router.config;

      this.router.config[2]['children'].push(...data);
      this.router.resetConfig(config);

    }, error => console.log(error));
  }
}
